<!--
@added | response from the server
-->
<template>
  <el-dialog title="–î–æ–±–∞–≤–∏—Ç—å –ø—Ä–µ–¥–º–µ—Ç" :visible.sync="inDialog" width="40%">
    <div>
      <span>–ù–∞–∑–≤–∞–Ω–∏–µ:</span>
      <el-input placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ" v-model="form.name" />
    </div>
    <span slot="footer" class="dialog-footer">
    <el-button @click="inDialog = false">–û—Ç–º–µ–Ω–∞</el-button>
    <el-button type="primary" @click="fetchAdd()">–î–æ–±–∞–≤–∏—Ç—å</el-button>
  </span>
  </el-dialog>
</template>

<script>
import axios from 'axios'

export default {
  props: {
    dialog: {
      type: Boolean,
      required: true
    },
    name: {
      type: String,
      required: false
    }
  },
  data () {
    return {
      form: {},
      inDialog: false,
      loading: false
    }
  },
  methods: {
    fetchAdd () {
      this.loading = true

      axios.post('subjects', this.form)
        .then(res => {
          this.$emit('added', res.data)
          this.$message({ type: 'success', message: '–î–æ–±–∞–≤–ª–µ–Ω–æ' })
          this.form = {}
          this.loading = false
          this.inDialog = false
        })
        .catch(() => {
          this.$message({ type: 'error', message: '–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞' })
          this.loading = false
        })
    }
  },
  watch: {
    dialog () {
      this.inDialog = true
      this.form.name = this.name
    }
  }
}
</script>



