<!--
@deleted | index
@edited | response from the server | index
-->
<template>
  <el-dialog :title="item.name" :visible.sync="inDialog" width="40%">
    <div>
      <span>–ù–æ–≤–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ:</span>
      <el-input placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ" v-model="inItem.name" />
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button type="danger" class="delete" icon="el-icon-delete" @click="openDelete()" size="small"
                 :loading="loading" :disabled="loading" />
      <el-button @click="inDialog = false">–û—Ç–º–µ–Ω–∞</el-button>
      <el-button type="primary" @click="fetchEdit()" :loading="loading" :disabled="loading">
        –ò–∑–º–µ–Ω–∏—Ç—å
      </el-button>
    </span>
  </el-dialog>
</template>

<script>
import axios from 'axios'

export default {
  props: {
    dialog: {
      type: Boolean,
      required: true
    },
    item: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
      required: false
    }
  },
  data () {
    return {
      inItem: {},
      inDialog: false,
      loading: false
    }
  },
  methods: {
    fetchEdit () {
      this.loading = true

      axios.put('subjects/' + this.inItem.id, this.inItem)
        .then(res => {
          this.$emit('edited', res.data, this.index)
          this.$message({ type: 'success', message: '–û—Ç—Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–æ' })
          this.loading = false
          this.inDialog = false
        })
        .catch(() => {
          this.$message({ type: 'error', message: '–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞' })
          this.loading = false
        })
    },
    fetchDelete () {
      this.loading = true

      axios.delete('subjects/' + this.inItem.id)
        .then(() => {
          this.$emit('deleted', this.index)
          this.$message({ type: 'success', message: '–£–¥–∞–ª–µ–Ω–æ' })
          this.loading = false
          this.inDialog = false
        })
        .catch(() => {
          this.$message({ type: 'error', message: '–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞' })
          this.loading = false
        })
    },
    openDelete () {
      this.$confirm('–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å —ç—Ç–æ—Ç –ø—Ä–µ–¥–º–µ—Ç?', this.item.name, {
        confirmButtonText: '–î–∞',
        cancelButtonText: '–û—Ç–º–µ–Ω–∞',
        type: 'warning'
      }).then(() => {
        this.fetchDelete()
      }).catch(() => {})
    }
  },
  watch: {
    dialog () {
      this.inItem = Object.assign({}, this.item)
      this.inDialog = true
    }
  }
}
</script>

<style lang="scss" scoped>
.delete {
  float: left;
  height: 40px;
}
</style>



