<template>
  <el-menu :default-active="routeFullPath" router>
    <el-menu-item v-for="(item, index) in items" :key="index" :index="'/admin' + item.route">
      <i class="material-icons">{{ item.icon }}</i>
      <span>{{ item.name }}</span>
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  data () {
    return {
      // TODO Role v-if
      items: [
        { route: '', name: '–ì–ª–∞–≤–Ω–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞', icon: 'dashboard' },
        { route: '/teachers', name: '–ü—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª–∏', icon: 'people' },
        { route: '/subjects', name: '–ü—Ä–µ–¥–º–µ—Ç—ã', icon: 'book' },
        { route: '/settings', name: '–ù–∞—Å—Ç—Ä–æ–π–∫–∏', icon: 'settings' }
      ]
    }
  },
  computed: {
    routeFullPath () {
      return this.$route.fullPath
    }
  }
}
</script>

<style lang="scss" scoped>
.el-menu {
  border-right: 0;
}

.material-icons {
  font-size: 20px;
  width: 24px;
  margin-right: 5px;
  text-align: center;
}
</style>



